@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Caveat:wght@500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;0,400;0,600;0,700;1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Londrina+Outline&display=swap);

body {
    background-color: #010204;;
    
}

.cocktail {
    width: 100%;
    background-color: #010204;
    margin: 5% 0 5% 0;
    padding: 0;
    display: flex;
    justify-content: center;
}

.container{
    padding: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border-width: 1px; */
    border: 2px solid white;
    border-radius: 14px;
    width: 300px;
    background-color: #111111;
}
.cocktail_img {
    width: 200px;
    border-radius: 50%;
    
    border: 3px solid #fff;
    padding: 0.5%;
    -webkit-filter: drop-shadow(5px 5px 6px #4444dd);
            filter: drop-shadow(5px 5px 6px #4444dd);
    /* background-color: #fff; */
}
.cocktail_name {
    padding: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-size: 34px;
    text-transform: uppercase;
    font-weight: 800;
    font-style: italic;
    text-align: center;
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
    animation: glow 1.5s ease-in-out infinite alternate;

}
 @keyframes glow {
  from {
    text-shadow:
    0 0 3px #fff,
    0 0 3px #fff,
    0 0 5px #e60073,
    0 0 5px #e60073,
    0 0 6px #e60073,
    0 0 7px #e60073,
    0 0 7px #e60073;
  }
  to {
    text-shadow:
    0 0 5px #fff,
    0 0 6px #ff4da6,
    0 0 7px #ff4da6,
    0 0 7px #ff4da6,
    0 0 8px #ff4da6,
    0 0 9px #ff4da6,
    0 0 9px #ff4da6;
  }
}


.how_to {
    text-align: center;
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
    /* text-transform: uppercase; */
    text-shadow: -1px 0 white, 0 1px white, 1px 0 white, 0 -1px white;
    font-weight: 600;
    border-bottom: 2px solid white;
    
}

.cocktail_recipe {
    display: flex;
    justify-content: space-around;
    /* background-color: #686d76; */
}

.cocktail_recipe .ingredients {
    width: 140px;
}

.cocktail_recipe .measurements {
    width: 140px;
}

.ingredients, .measurements {
    list-style: none;
}

ul {
    padding: 0;
}

li {
    padding: 4px 0 4px 0;
    font-size: 22px;
    font-weight: 600;
    font-family: 'Caveat', cursive;
    letter-spacing: 0.5px;
    color:white
     /* text-transform: uppercase; */
}

.measurements li {
    display: flex;
    justify-content: flex-end;
}


hr {
    border: 1px solid white;
    height: 2px;
}

.cocktail_glass {
    /* text-align: right; */
    margin: 8% 0 8% 0;
    color:white;
    font-family: 'Titillium Web', sans-serif;
    font-style: italic;
    /* font-family: 'Caveat', cursive; */
    font-size: 18px;
    /* text-transform: uppercase; */
    /* border-bottom: 2px solid white; */
}

.cocktail_instruction {
    width: 300px;
    font-family: 'Titillium Web', sans-serif;
    color:white;
    font-size: 14px;
    margin: 4% 0 0 0;
    text-align: center;
    text-align: justify; 
    text-justify: inter-word;
}
