.home_container {
     max-width: 100%;
}
.home_container .main_img {
    width: 100%;
    object-fit: cover;
}

.about_container {
    width: 100%;
    margin: 50% 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about_container p {
    margin: 2%;
    color: white;
    font-size: 14px;

}
.nav_container {
    background-color: #010204;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 60px;
    margin-top: 0;
}

.nav_menus {
    font-family: 'Londrina Outline', cursive;
    font-size: 34px;
    font-weight: 600;
    color: #ff4da6;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    margin: 14px;
  text-shadow:
    0 0 4px #0e11b9,
    0 0 10px #e60073,
    0 0 20px #0e11b9
}

.nav_menus:hover {
    color: white;
    
}

.nav_menus:active {
    color: white;
}

.nav_menus:focus {
    border-bottom: 2px solid white;
    padding-bottom: 36px;

}
.scroll-to-top {  
    display: flex;
    justify-content: flex-end;
    margin: 3% 2% 3% 2%;
}
.upButton{   
    cursor: pointer;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: white;
    box-shadow: 0 3px 15px -2px;
    transition-duration: .1s;

}

.upButton:active{
    transform: scale(.9);
}

.up {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 29%;
    font-size: 30px;
    color: black;
}
