.scroll-to-top {  
    display: flex;
    justify-content: flex-end;
    margin: 3% 2% 3% 2%;
}
.upButton{   
    cursor: pointer;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: white;
    box-shadow: 0 3px 15px -2px;
    transition-duration: .1s;

}

.upButton:active{
    transform: scale(.9);
}

.up {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 29%;
    font-size: 30px;
    color: black;
}